<script>
  import Background from "./Background.svelte";

  let r = 0;
  let g = 0;
  let b = 0;
  let opacity = 1;
</script>

<style>
  .choose {
    margin: 2rem;
    display: flex;
    align-items: center;
    justify-content: left;
    flex-direction: column;
  }

  .choose p {
    font-size: 1.7rem;
    margin: 1rem;
  }

  input[type="number"] {
    width: 7rem;
    text-align: center;
    margin: 1rem 0;
    border-radius: 3px;
    border: 1px solid #ddd;
    /* padding: 1.5rem 0; */
    padding: 0.5rem 1rem;
  }

  .container {
    display: flex;
    min-height: 70rem;
  }

  .wrapper {
    flex: 1 0 20%;
    height: 100%;
    border-radius: 3px;
    border: 1px solid #eee;
    margin-right: 1.5rem;
  }
  .back {
    flex: 1 0 80%;
    height: 54.6rem;
    margin-right: 1.5rem;
  }

  @media only screen and (max-width: 900px) {
    .wrapper {
      flex: 1 0 40%;
    }
    .back {
      flex: 1 0 60%;
    }
  }

  @media only screen and (max-width: 400px) {
    .container {
      flex-direction: column;
    }
    .wrapper {
      flex: 1 0 100%;
      margin: 0;
    }
    .back {
      flex: 1 0 100%;
      margin: 0;
    }
  }
  .title {
    text-align: center;
    font-size: 3.5rem;
    font-weight: lighter;
    margin-bottom: 5rem;
  }
  input[type="range"] {
    height: 23px;
    -webkit-appearance: none;
    width: 100%;
  }
  input[type="range"]:focus {
    outline: none;
  }
  input[type="range"]::-webkit-slider-runnable-track {
    width: 100%;
    height: 6px;
    cursor: pointer;
    box-shadow: 0px 0px 0px #000000;
    background: #e1e3e1;
    border-radius: 50px;
    border: 0px solid #000000;
  }

  input[type="range"]:focus::-webkit-slider-runnable-track {
    background: #e1e3e1;
  }
  input[type="range"]::-moz-range-track {
    width: 100%;
    height: 6px;
    cursor: pointer;

    box-shadow: 0px 0px 0px #000000;
    background: #e1e3e1;
    border-radius: 50px;
    border: 0px solid #000000;
  }

  input[type="range"]::-ms-track {
    width: 100%;
    height: 6px;
    cursor: pointer;

    background: transparent;
    border-color: transparent;
    color: transparent;
  }
  input[type="range"]::-ms-fill-lower {
    background: #e1e3e1;
    border: 0px solid #000000;
    border-radius: 100px;
    box-shadow: 0px 0px 0px #000000;
  }
  input[type="range"]::-ms-fill-upper {
    background: #e1e3e1;
    border: 0px solid #000000;
    border-radius: 100px;
    box-shadow: 0px 0px 0px #000000;
  }

  input[type="range"]:focus::-ms-fill-lower {
    background: #e1e3e1;
  }
  input[type="range"]:focus::-ms-fill-upper {
    background: #e1e3e1;
  }

  input[type="range"]::-webkit-slider-thumb {
    box-shadow: 0px 0px 0px #000000;
    border: 1px solid #c7c7c7;
    height: 16px;
    width: 16px;
    border-radius: 18px;

    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -5.5px;
  }

  input[type="range"]::-moz-range-thumb {
    box-shadow: 0px 0px 0px #000000;
    border: 1px solid #c7c7c7;
    height: 16px;
    width: 16px;
    border-radius: 18px;
    cursor: pointer;
  }

  .range-red::-webkit-slider-thumb {
    background: #ff4f78;
  }
  .range-green::-webkit-slider-thumb {
    background: #7be86b;
  }
  .range-blue::-webkit-slider-thumb {
    background: #54afff;
  }
  .range-opacity::-webkit-slider-thumb {
    background: #d3dde3;
  }

  .range-red::-moz-range-thumb {
    background: #ff4f78;
  }
  .range-green::-moz-range-thumb {
    background: #7be86b;
  }
  .range-blue::-moz-range-thumb {
    background: #54afff;
  }
  .range-opacity::-moz-range-thumb {
    background: #d3dde3;
  }
</style>

<h1 class="title">Pick a color</h1>
<div class="container">

  <div class="wrapper">
    <div class="choose">
      <p>Red</p>
      <input
        class=" range range-red"
        type="range"
        min="0"
        max="255"
        step="1"
        bind:value={r} />
      <input type="number" bind:value={r} min="0" max="255" />
    </div>

    <div class="choose">
      <p>Green</p>
      <input
        class=" range range-green"
        type="range"
        min="0"
        max="255"
        step="1"
        bind:value={g} />
      <input type="number" bind:value={g} min="0" max="255" />
    </div>

    <div class="choose">
      <p>Blue</p>
      <input
        class=" range range-blue"
        type="range"
        min="0"
        max="255"
        step="1"
        bind:value={b} />
      <input type="number" bind:value={b} min="0" max="255" />
    </div>

    <div class="choose">
      <p>Opacity</p>
      <input
        class=" range range-opacity"
        type="range"
        min="0"
        max="1"
        step="0.1"
        bind:value={opacity} />
      <input type="number" bind:value={opacity} step="0.1" min="0" max="1" />
    </div>
  </div>

  <div class="back">
    <Background {r} {g} {b} {opacity} />
  </div>

</div>
