<script>
  export let r;
  export let g;
  export let b;
  export let opacity;

  $: rgba = `rgba(${r},${g},${b},${opacity})`;
  $: rgbaNoText = `(${r},${g},${b},${opacity})`;
  $: hex = `#${fullColorHex(r)}${fullColorHex(g)}${fullColorHex(b)}`;

  const rgbToHex = rgb => {
    let hex = Number(rgb).toString(16);
    if (hex.length < 2) {
      hex = "0" + hex;
    }
    return hex;
  };

  const fullColorHex = (r, g, b) => {
    const red = rgbToHex(r);
    return red;
  };

  const copyToClipboard = str => {
    const el = document.createElement("textarea");
    el.value = str;
    document.body.appendChild(el);
    el.select();
    document.execCommand("copy");
    document.body.removeChild(el);
  };

  const makeColorDarker = rgba => {
    const rgbaSplited = rgba
      .replace("(", "")
      .replace(")", "")
      .split(",");

    rgbaSplited[rgbaSplited.length - 1] = "1";
    return `rgba(${rgbaSplited.join(",")})`;
  };

  const alertCopied = color => {
    copyToClipboard(color);
    alert("Color copied!");
  };
</script>

<style>
  .canvas {
    height: 94%;
    margin: 1.5rem;
    border-radius: 3px;
    margin-right: 1.5rem;
  }

  .info {
    margin-top: 2.5rem;
  }
  .info__text {
    margin-top: 3rem;
    text-align: center;
    font-size: 1.8rem;
  }

  .select-color {
    cursor: pointer;
    transition: border 0.3s;
    position: relative;
    min-width: 10rem;
  }

  .select-color::before {
    content: "";
    display: block;
    position: absolute;
    bottom: -0.3rem;
    left: 0;
    width: 0%;
    height: 2px;
    background: currentColor;
    border-radius: 3px;
    transition: width 0.5s;
  }

  .select-color::after {
    content: "";
    display: block;
    position: absolute;
    top: -1rem;
    right: -4rem;
    height: 100%;
    width: 10rem;
    border: 1px solid transparent;
    color: #aaa;
    font-size: 1.4rem;
    padding: 0.5rem;
    border-radius: 3px;
    transition-property: top;
    transition: 0.3s;
  }

  .select-color:hover::before {
    width: 100%;
  }

  .select-color:hover::after {
    content: "Click to copy";
    top: -2.5rem;
    right: -4rem;
    height: 100%;
    width: 10rem;
    border: 1px solid #eee;
  }

  @media only screen and (max-width: 600px) {
    .info__text {
      font-size: 1.5rem;
      text-align: center;
    }
  }

  @media only screen and (max-width: 400px) {
    .canvas {
      height: 50%;
    }
  }
</style>

<div class="canvas" style="background:{rgba}" />

<div class="info">
  <p class="info__text">
    Color in RGBA:
    <span
      class="select-color"
      style="color:{makeColorDarker(rgbaNoText)}"
      on:click={() => alertCopied(rgba)}>
      rgba{rgbaNoText}
    </span>
  </p>
  <p class="info__text">
    Color in HEX:
    <span
      class="select-color"
      style="color:{makeColorDarker(rgbaNoText)}"
      on:click={() => alertCopied(hex)}>
       {hex}
    </span>
  </p>
</div>
